.page {
  text-align: center;
  background-color: #282c34;
  min-height: calc(100vh + 100px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

h1, h2, h3, h4, h5, h6 {
  display: block;
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}

.App-logo {
  height: 8vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

#built-with {
  transform: translateX(-10px);
  display: flex;
  align-items: center;
}

.card_container {
  background-color: rgba(0,0,0,.3);
  min-height: 700px;
  width: 317px;
  font-size: calc(10px + 2vmin);
  color: white;
  position: relative;
  border-radius: 15px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Info */

#headshot_container {
  position: relative;
  width: 100%;
  height: 317px;
}

#headshot {
  object-fit: cover;
  object-position: 0% 15%;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  height: 317px;
  width: 100%;
}

#name {
  font-size: 25px;
  margin-top: 25px;
}

#role {
  font-size: 13px;
  color: #f3bf99;
  letter-spacing: 1px;
}

#website {
  font-size: 13px;
  color: #f5f5f5;
  font-weight: 400;
}

  /* Buttons */

  #buttons {
    display: flex;
    margin: 15px 35px;
    justify-content: space-around;
  }

  button {
    padding: 8px 15px;
    display: flex;
    width: 100px;
    align-items: center;
    justify-content: space-around;
    border-radius: 4px;
    border: none;
    font: Arial;
    font-weight: 500;
    cursor: pointer;
  }
  
  button img {
    margin-right: 10px;
  }

  #linkedin_button {
    color: white;
    background-color: #5093e2;
  }

/* About */

#about_section {
  text-align: left;
  margin: 30px 35px;
}

#about_section h5 {
  font-size: 16px;
}

#about_section p {
  font-size: 13px;
  margin-bottom: 85px;
}


/* Footer */

.footer_container {
  height: 64px;
  width: 100%;
  background-color: rgba(0,0,0,.4);
  display: flex;
  align-items: center;
  position: absolute;
  bottom: 0;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
}

#logo_container {
  margin: 0px 75px;
  width: 60%;
  display: flex;
  justify-content: space-between;
}

.foot_logo {
  height: 25px;
  width: 25px;
}